.page-header {
  @include wrapper;

  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 32px 15px 10px 15px;

  font-family: $font-deafult;
  font-size: 15px;
  line-height: 18px;
  font-weight: 500;
  font-style: normal;
  text-align: left;
  color: $main-black;
}

.page-header__tel {
  display: block;

  color: $main-black;

  &:hover,
  &:focus {
    color: $yellow;
  }
}

.page-header__logo {

}

.page-header__link {
  padding: 0;

  font-family: $font-deafult;
  font-size: 15px;
  line-height: 18px;
  font-weight: 500;
  color: $main-black;
  text-transform: uppercase;

  background-color: $white;
  border: none;
  border-bottom: 1px dashed $main-black;
  cursor: pointer;

  &:hover,
  &:focus {
    color: $yellow;

    border-color: $yellow;
  }
}

@media (max-width: $mobile-width-only) {
  .page-header {
    position: relative;

    flex-direction: column;
    align-items: flex-end;
    padding: 20px 9px 10px 16px;
  }

  .page-header__tel {
    margin-bottom: 8px;

    font-size: 14px;
    line-height: 17px;
  }

  .page-header__logo {
    position: absolute;
    top: 20px;
    left: 16px;
  }

  .page-header__link {
    font-size: 14px;
    line-height: 17px;
  }
}
