@charset "UTF-8";
@font-face {
  font-family: "Fira Sans";
  src: local("FiraSans"), local("FiraSans-Regular"), url("../fonts/fira-sans-regular.woff2") format("woff2"), url("../font/fira-sans-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "FiraSans";
  src: local("FiraSans Medium"), local("FiraSans-Medium"), url("../fonts/fira-sans-medium.woff2") format("woff2"), url("../fonts/fira-sans-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "FiraSans";
  src: local("FiraSans Bold"), local("FiraSans-Bold"), url("../fonts/fira-sans-bold.woff2") format("woff2"), url("../fonts/fira-sans-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: local("Montserrat Bold"), local("Montserrat-Bold"), url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Raleway";
  src: local("Raleway Extra Bold"), local("Raleway-Extra-Bold"), url("../fonts/raleway-extra-bold.woff2") format("woff2"), url("../fonts/raleway-extra-bold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Raleway";
  src: local("Raleway Black"), local("Raleway-Black"), url("../fonts/raleway-black.woff2") format("woff2"), url("../fonts/raleway-black.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 170%;
  font-weight: 400;
  font-style: normal;
  text-align: left;
  color: #484848;
  background-color: #ffffff; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

.page-header {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 32px 15px 10px 15px;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 18px;
  font-weight: 500;
  font-style: normal;
  text-align: left;
  color: #484848; }
  @media (min-width: 1175px) {
    .page-header {
      width: 1175px; } }
  @media (max-width: 1175px) {
    .page-header {
      width: 100%; } }
  @media (max-width: 320px) {
    .page-header {
      width: 320px; } }

.page-header__tel {
  display: block;
  color: #484848; }
  .page-header__tel:hover, .page-header__tel:focus {
    color: #FFC341; }

.page-header__link {
  padding: 0;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 18px;
  font-weight: 500;
  color: #484848;
  text-transform: uppercase;
  background-color: #ffffff;
  border: none;
  border-bottom: 1px dashed #484848;
  cursor: pointer; }
  .page-header__link:hover, .page-header__link:focus {
    color: #FFC341;
    border-color: #FFC341; }

@media (max-width: 767px) {
  .page-header {
    position: relative;
    flex-direction: column;
    align-items: flex-end;
    padding: 20px 9px 10px 16px; }
  .page-header__tel {
    margin-bottom: 8px;
    font-size: 14px;
    line-height: 17px; }
  .page-header__logo {
    position: absolute;
    top: 20px;
    left: 16px; }
  .page-header__link {
    font-size: 14px;
    line-height: 17px; } }

.page-main {
  flex-grow: 1; }

.page-footer {
  flex-grow: 0;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-size: 12px;
  line-height: 150%;
  font-weight: 400;
  font-style: normal;
  text-align: center;
  color: #484848;
  border-top: 1px solid rgba(72, 72, 72, 0.2); }

.page-footer__wrapper {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 24px 15px 24px 15px; }
  @media (min-width: 1175px) {
    .page-footer__wrapper {
      width: 1175px; } }
  @media (max-width: 1175px) {
    .page-footer__wrapper {
      width: 100%; } }
  @media (max-width: 320px) {
    .page-footer__wrapper {
      width: 320px; } }

.page-footer__copyright {
  margin: 0;
  opacity: 0.5; }

.page-footer__social-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between; }

.page-footer__social-item {
  margin-left: 20px; }
  .page-footer__social-item:first-child {
    margin-left: 0; }

.page-footer__social-item a {
  display: block; }

.page-footer__social-item svg {
  fill: rgba(0, 0, 0, 0.16); }
  .page-footer__social-item svg:hover, .page-footer__social-item svg:focus {
    fill: #FFC341; }

@media (max-width: 767px) {
  .page-footer__wrapper {
    flex-direction: column;
    padding: 32px 16px 32px 16px; }
  .page-footer__logo {
    margin-bottom: 24px; }
  .page-footer__copyright {
    margin-bottom: 28px; }
  .page-footer__social-list {
    margin: 0 auto;
    flex-wrap: wrap; } }
